<style scoped>
    .cart_base{
        cursor: pointer;
    }
    .cart-count {
        position: absolute;
        top: 0;
        right: 0;
        background-color: black;
        color: white;
        border-radius: 50%;
        width: 10px;
        height: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 12px;
    }
    .badge {
        display: inline-block;
        padding: 4px 8px;
        font-size: 12px;
        font-weight: bold;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        background-color: #0d192a;
        color: white;
        border-radius: 50%;
    }
    .nav_item {
        line-height: 2.0;
        font-size: 14px;
        word-break: break-word;
        text-align: left;
        font-weight: 900;
        color : black;
    }
    .drrop_item {
        line-height: 1.5;
        font-size: 12px;
        word-break: break-word;
        text-align: left;
        font-weight: 400;
        color : black;
    }
    @media only screen and (max-width: 767px) {
        .NavbarBody {
            width: 70% !important;
        }
    }
    
</style>>
<template>
    <div>
        <nav class="navbar fixed-top" style="box-shadow: 0 2px 6px 0 rgba(0,0,0,.06); background-color: white;">
            <div class="container-fluid">
                <img :src="img.title_icon" style="height: 2.5rem;">
                <div class="d-flex align-items-center">
                    <span class="navbar-toggler" data-bs-toggle="collapse" aria-expanded="false" aria-label="Toggle navigation" style="margin-right: 5px; border: none;">
                        <div style="position: relative;" class="cart_base">
                            <svg xmlns="http://www.w3.org/2000/svg" v-if="true" width="28" height="28" fill="currentColor" class="bi bi-cart3" viewBox="0 0 16 16">
                                <path d="M0 1.5A.5.5 0 0 1 .5 1H2a.5.5 0 0 1 .485.379L2.89 3H14.5a.5.5 0 0 1 .49.598l-1 5a.5.5 0 0 1-.465.401l-9.397.472L4.415 11H13a.5.5 0 0 1 0 1H4a.5.5 0 0 1-.491-.408L2.01 3.607 1.61 2H.5a.5.5 0 0 1-.5-.5zM3.102 4l.84 4.479 9.144-.459L13.89 4H3.102zM5 12a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm7 0a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm-7 1a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm7 0a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"/>
                            </svg>
                            <svg xmlns="http://www.w3.org/2000/svg" v-if="false" width="28" height="29" fill="currentColor" class="bi bi-cart4" viewBox="0 0 16 16">
                                <path d="M0 2.5A.5.5 0 0 1 .5 2H2a.5.5 0 0 1 .485.379L2.89 4H14.5a.5.5 0 0 1 .485.621l-1.5 6A.5.5 0 0 1 13 11H4a.5.5 0 0 1-.485-.379L1.61 3H.5a.5.5 0 0 1-.5-.5zM3.14 5l.5 2H5V5H3.14zM6 5v2h2V5H6zm3 0v2h2V5H9zm3 0v2h1.36l.5-2H12zm1.11 3H12v2h.61l.5-2zM11 8H9v2h2V8zM8 8H6v2h2V8zM5 8H3.89l.5 2H5V8zm0 5a1 1 0 1 0 0 2 1 1 0 0 0 0-2zm-2 1a2 2 0 1 1 4 0 2 2 0 0 1-4 0zm9-1a1 1 0 1 0 0 2 1 1 0 0 0 0-2zm-2 1a2 2 0 1 1 4 0 2 2 0 0 1-4 0z"/>
                            </svg>
                            <div class="cart-count" v-if="cart.length > 0">
                                <span class="badge">{{ cart.length }}</span>
                            </div>
                        </div>
                    </span>
                    <span class="navbar-toggler" id="offcanvasClose" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar" style="border : none;">
                        <!-- <span class="navbar-toggler-icon"></span> -->
                        <svg xmlns="http://www.w3.org/2000/svg" width="28" height="28" fill="currentColor" class="bi bi-justify-right" viewBox="0 0 16 16">
                            <path fill-rule="evenodd" d="M6 12.5a.5.5 0 0 1 .5-.5h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5zm-4-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5zm0-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5zm0-3a.5.5 0 0 1 .5-.5h11a.5.5 0 0 1 0 1h-11a.5.5 0 0 1-.5-.5z"></path>
                        </svg>
                    </span>
                </div>
                <div class="NavbarBody offcanvas offcanvas-end" tabindex="-1" id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel">
                    <div class="offcanvas-header">
                        <h5 class="offcanvas-title" id="offcanvasNavbarLabel">&nbsp;</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                    </div>
                    <div class="offcanvas-body">
                        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
                            <li v-for="(item, key) in navData" :key="key" :class="{'nav-item': !item.children, 'nav-item dropdown': item.children, 'nav_item' : true}">
                                <a class="nav-link nav_item" href="#" role="button" v-if="!item.children" v-on:click="setTyp(item.optionId, item.name)">{{ item.name }}</a>
                                <a class="nav-link dropdown-toggle nav_item" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false" v-else>{{ item.name }}</a>
                                <ul class="dropdown-menu" v-if="item.children" style="border-width: inherit;">
                                    <li v-for="(subItem, index) in item.children" :key="subItem.name">
                                        <a class="dropdown-item drrop_item" href="#" v-on:click="setTyp(subItem.optionId, item.name, subItem.name)">{{ subItem.name }}</a>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </nav>
    </div>
</template>

<script>
import axios from 'axios'
axios.defaults.timeout = 6000
//img
import title_icon from "../assets/img/title.webp"
export default {
    name: 'index',
    components: {},
    data () {
        return {
            img : {
                "title_icon" : title_icon,
            },
            navData : {

            },
            cart : [1],
            showCart: false
        }
    },
    watch : {
        
    },
    methods: {
        setTyp(hid = '', name = '', sub_nam = ''){
            // console.log(hid, name, sub_nam);
            let temp = {};
            temp.hid = hid;
            temp.nam = {'name' : name, 'sub_nam' : sub_nam};
            this.$emit('setChooseType', temp);
            document.getElementById('offcanvasClose').click();
        },
        alertModal(title='系統訊息', msg = '') {
            var myModal = new bootstrap.Modal(document.getElementById('this.alertModal'), {
                keyboard: false
            });
            document.getElementById('alertTitle').innerHTML = title;
            document.getElementById('alertMsg').innerHTML = msg;
            myModal.show();
        },
        convertHeaderData(jsonData) {
            const convertedData = [];

            // 处理每个项
            for (let item of jsonData) {
                const convertedItem = {
                label: item.name,
                dropdown: [],
                choose: 0
                };

                // 处理子项
                if (item.children) {
                for (let child of item.children) {
                    convertedItem.dropdown.push({
                    label: child.name
                    });
                }
                }

                convertedData.push(convertedItem);
            }
            // console.log(convertedData);
            return convertedData;
        }
    },
    async mounted() {
        let param = {};
        let rt = await this.$gows.callAPI('products/searchOptions', param);
        // console.log(rt);
        this.$set(this, 'navData', rt);
        // this.navData = tempData;   
    }
}
</script>



