import {Base64} from 'js-base64'
import axios from 'axios'

axios.defaults.timeout = 60000

var gows = {
    callAPI : async function (tag, param){
        let wsParam = '';
        for(let inx in param) {
            wsParam += (wsParam) ? '&' + inx + '=' + param[inx] : inx + '=' + param[inx];
        }
        return new Promise((resolve, reject) => {
            axios.get('https://app.hosetee.com/api/' + tag + '?' + wsParam).then(result => {
                let rt = result.data;
                try {
                    resolve(rt);
                } catch (e) {
                    console.log(e)
                }
            }),err => {
                reject(err);
            }
        });
    }
}
export default gows = gows;


